.header {
  min-height: 8vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: calc(10px + 2vmin);
  padding: 0px 10px
}

.body {
  width: 100vw;
  height: 86vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  padding: 0px 10px
}

.category {
  display:flex;
  align-items: center;
  justify-content: space-between;
  font-size: calc(8px + 2vmin);
}

.categoryButton {
  width: 30%;
  height: 40px;
  margin: 5px;
}

.categorySentence {
  width: 90%;
  font-size: 12px;
}

.categoryQuantity {
  width: 20%;
  font-size: 12px;
}

.categoryButtonCategory {
  width: 30%;
  display:flex;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
}


.footer {
  min-height: 6vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  padding: 0px 10px
}

.question {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
}

.question .sentence {
  font-size: calc(12px + 2vmin);
  margin: 5px;
}

.question .quantity {
  font-size: calc(12px + 2vmin);
  margin: 5px;
}

.detail-modal-header {
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.detail-modal-body {
  display: flex;
  flex-direction: column;
}

.knowMore {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.knowMore .sentence-quantity {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.knowMore .sentence {
  font-size: calc(12px + 2vmin);
}

.knowMore .quantity {
  font-size: calc(12px + 2vmin);
  font-weight: bold;
}

.knowMore .title {
  font-size: calc(8px + 2vmin);
  text-decoration: underline;
}

.knowMore .text {
  font-size: calc(6px + 2vmin);
}

